.message-user {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  color: #ffffff;
  border-radius: 18px 18px 4px 18px;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
  padding: 12px 16px;
  font-weight: 500;
  max-width: 85%;
  margin-left: auto;
  word-wrap: break-word;
}
.message-bot {
  background: #f8fafc;
  color: #334155;
  border-radius: 18px 18px 18px 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  padding: 12px 16px;
  max-width: 85%;
  border: 1px solid #e2e8f0;
  word-wrap: break-word;
}
.dark .message-bot {
  background: #1e293b;
  color: #f1f5f9;
  border-color: #334155;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}
.chat-input {
  background: #ffffff;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  padding: 12px 16px;
  font-size: 16px;
  color: #334155;
  transition: all 0.2s ease;
  outline: none;
  resize: none;
}
.dark .chat-input {
  background: #1e293b;
  border-color: #334155;
  color: #f1f5f9;
}
.chat-input:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  transform: translateY(-1px);
}
.dark .chat-input:focus {
  border-color: #60a5fa;
  box-shadow: 0 0 0 3px rgba(96, 165, 250, 0.1);
}
.btn-send {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  color: #ffffff;
  border: none;
  border-radius: 10px;
  padding: 12px 20px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}
.btn-send:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(59, 130, 246, 0.4);
}
.btn-send:active {
  transform: translateY(0);
}
.btn-send:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}
.animate-fadeInUp {
  animation: fadeInUp 0.4s ease-out;
}
.animate-pulse-slow {
  animation: pulse 2s ease-in-out infinite;
}
.chat-container {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  line-height: 1.6;
}
.message-timestamp {
  font-size: 11px;
  opacity: 0.6;
  margin-top: 4px;
  font-weight: 400;
}
.typing-indicator {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  background: #f1f5f9;
  border-radius: 18px 18px 18px 4px;
  max-width: 85%;
}
.dark .typing-indicator {
  background: #1e293b;
}
.typing-dots {
  display: flex;
  align-items: center;
  gap: 4px;
}
.typing-dots span {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #64748b;
  animation: typing 1.4s ease-in-out infinite;
}
.dark .typing-dots span {
  background: #94a3b8;
}
.typing-dots span:nth-child(1) {
  animation-delay: 0s;
}
.typing-dots span:nth-child(2) {
  animation-delay: 0.2s;
}
.typing-dots span:nth-child(3) {
  animation-delay: 0.4s;
}
@keyframes typing {
  0%, 60%, 100% {
    transform: translateY(0);
    opacity: 0.4;
  }
  30% {
    transform: translateY(-10px);
    opacity: 1;
  }
}
.glass-effect {
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}
.dark .glass-effect {
